import styled from 'styled-components';

const HeaderStyle = styled.section`
  margin: 0px auto;
  padding: 0;
  border: 0;
  width: calc(100% - 60px);
  height: 100%;
  max-width: 1740px;
  position: relative;
  display: block;
  .scrolling-navbar {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
`;

export default HeaderStyle;
